.btn__active{
    font-weight: bold;
    background-color: #64c37d;
    padding: 8px 12px;
    text-decoration: none;
    color: white;
    border-radius: 5px;
    cursor: pointer;
}

.btn {
    color: black;
    padding: 8px 12px;
    text-decoration: none;
    cursor: pointer;
  }
  
  .btn:hover:not(.btn__active) {
    background-color: #ddd;
    border-radius: 5px;
  }