table {
    font-family: Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    width: 100%;
    margin-top: 50px;
}
  
table td, table th {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: center;
}
  
table tr:nth-child(even) {
    background-color: #f2f2f2;
}
  
table tr:hover {
    background-color: #ddd;
}
  
table th {
    padding-top: 12px;
    padding-bottom: 12px;
    background-color: #64c37d;
    color: white;
}

